<template>
	<div class="ranking-filter">
		<div v-for="(item,index) in test" :key="index">
			<span :class="{rankingactive:currentIndex===index}"  @click="itemClick(index)">{{item}}</span>
		</div>
	</div>
</template>

<script>


export default {
  name: 'RankingFilterNavs',
  components:{
  },
  data(){
	return{
		currentIndex:2
	}
  },
  props:{
	test:{
		type:Array,
		default(){
			return[]
		}
	},
	filterArr:{
		type:Array,
		default(){
			return[]
		}
	},
  },
  methods:{
	itemClick(index){
		if(this.currentIndex===index)return
		this.currentIndex=index
		this.$emit("itemClcik",this.currentIndex)
	}
  }
}
</script>


