<template>
	<div id="customer-ranking">
		<!--顶部菜单-->
		<nav-bar class="home-nav-bar">
			<div slot="left" class="icon-left" @click="backClick">
				<img src="~assets/images/common/prev2.svg">
			</div>
			<div slot="center">
				销售客户排行
			</div>
			<div slot="right">
				图标
			</div>
		</nav-bar>
		<!--顶部菜单客户页面也用到所以抽出来-->
		<ranking-top-navs/>
		<!--数量和金额客户页面也用到所以抽出来-->
		<ranking-filter-navs :test="filterNavs" class="bghui" @itemClcik="rankClcik" ref="ranking"/>
		<!--滚动--->
		<scroll class="customer-ranking-scroller"
				ref="scroller"
				:probe-type=3
				@pullingup="loadMove"
		>
		<!--排行列表-->
		<ranking-content :customer-list="customerLists"/>
		</scroll>
	</div>
</template>

<script>

import NavBar from 'components/common/navbar/NavBar'
import Scroll from 'components/common/scroll/Scroll'
import RankingTopNavs from 'components/contents/rankingnavs/RankingTopNavs'
import RankingFilterNavs from 'components/contents/rankingnavs/RankingFilterNavs'
import RankingContent from './childComps/RankingContent'

import {itemCustomerMixin} from 'common/mixin'	


export default {
  name: 'CustomerRanking',
	components: {
		NavBar,
		Scroll,
		RankingTopNavs,
		RankingFilterNavs,
		RankingContent
	},
	mixins:[itemCustomerMixin],				//混入-获取订单列表
	data(){
		return{
			filterNavs:["货品","单据","金额","数量"],
		}
	},
	methods:{
		//返回按钮
		backClick(){
			//this.$router.replace("/customersdetail").catch(res=>{res})
			this.$router.go(-1)
		},
		//货品单据金额数量点击
		rankClcik(index){
			switch(index){
				case 0:
					this.sortType='productid'
					break
				case 1:
					this.sortType='documentnum'
					break
				case 2:
					this.sortType='money'
					break
				case 3:
					this.sortType='productnum'
					break
			}
		},
	},
	mounted(){
		this.$refs.ranking.currentIndex=0
	},
	computed:{
		ccustomerList(){//排序
			let arr= this.customerList.customers.list
			return arr.sort(this.compare(this.sortType))
		},
		customerLists(){
			//这里是通过vuex获取的
			/*let arr= this.$store.getters.getCustomerList.list*/
			//mixn里面定义的
			let arr= this.customerList.customers.list
			return  arr.sort(this.compare(this.sortType))		//排序
		}
	},
}
</script >

